<template>
  <b-container fluid>
                     <div class="iq-card">
                        <div class="iq-card-header d-flex justify-content-between">
                           <div class="iq-header-title">
                              <h4 class="card-title">Create New Link</h4>
                           </div>
                        </div>
                        <div class="iq-card-body">
                           <form>
                              <div class="input-group">
                                  <div class="input-group-append">
                                    <span class="input-group-text">Name</span>
                                </div>
                                <input type="name" class="form-control" id="name">
                                </div>
                                <p class="text-f mt-1">Enter the name of the link (free text). The name is not visible to referrals.</p>
                              <div class="form-group">
                              <div class="input-group mt-3">
                                  <div class="input-group-append z">
                                    <span class="x">Landing Page</span>
                                </div>
                                <select class="form-control">
                                 <option selected="">Open this select menu</option>
                                 <option value="1">User</option>
                                 <option value="2">Dashboard</option>
                                 <option value="3">Reports</option>
                              </select>
                                </div>
                                 <p class="text-f mt-1">Choose the page to which referrals will be redirected when they click on the link.</p>
                           </div>
                              <button type="submit" class="btn btn-success">Continue</button>
                           </form>
                        </div>
                     </div>
    <b-row>
      <b-col md="12">
        <iq-card>
          <template v-slot:headerTitle>
            <h4 class="card-title">IB Links</h4>
          </template>
          <template v-slot:body>
            <b-row>
              <b-col md="12" class="table-responsive">
                <b-table bordered hover :items="rows" :fields="columns" foot-clone>
                  <template v-slot:cell(name)="data">
                    <span v-if="!data.item.editable">{{ data.item.name }}</span>
                    <input type="text" v-model="data.item.name" v-else class="form-control">
                  </template>
                  <template v-slot:cell(position)="data">
                    <a href="#" v-if="!data.item.editable">{{ data.item.position }}</a>
                    <input type="text" v-model="data.item.position" v-else class="form-control">
                  </template>
                  <template v-slot:cell(office)="data">
                    <span v-if="!data.item.editable">{{ data.item.office }}</span>
                    <input type="text" v-model="data.item.office" v-else class="form-control">
                  </template>
                  <template v-slot:cell(age)="data">
                    <span v-if="!data.item.editable">{{ data.item.age }}</span>
                    <input type="text" v-model="data.item.age" v-else class="form-control">
                  </template>
                  <template v-slot:cell(action)="data">
                    <b-button variant=" iq-bg-success mr-1 mb-1" size="sm" @click="edit(data.item)" v-if="!data.item.editable"><i class="ri-ball-pen-fill m-0"></i></b-button>
                    <b-button variant=" iq-bg-success mr-1 mb-1" size="sm" @click="submit(data.item)" v-else>Ok</b-button>
                    <b-button variant=" iq-bg-danger" size="sm" @click="remove(data.item)"><i class="ri-delete-bin-line m-0"></i></b-button>
                  </template>
                </b-table>
              </b-col>
            </b-row>
          </template>
        </iq-card>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
import { core } from '../../config/pluginInit'

export default {
  name: 'Links',
  mounted () {
    core.index()
  },
  methods: {
    add () {
      const obj = this.default()
      this.rows.push(obj)
    },
    default () {
      return {
        id: this.rows.length,
        name: '',
        position: '',
        office: '',
        age: '',
        editable: false
      }
    },
    edit (item) {
      item.editable = true
    },
    submit (item) {
      item.editable = false
    },
    remove (item) {
      const index = this.rows.indexOf(item)
      this.rows.splice(index, 1)
    }
  },
  data () {
    return {
      columns: [
        { label: 'Name', key: 'name', class: 'text-left' },
        { label: 'Link', key: 'position', class: 'text-left' },
        { label: 'Clicks', key: 'clicks', class: 'text-left' },
        { label: 'Registrations', key: 'registrations', class: 'text-left' },
        { label: 'Action', key: 'action', class: 'text-center' }
      ],
      rows: [
        {
          id: 1,
          name: 'wp',
          position: 'https://demo.deneme.com/ib/links/go/64',
          clicks: '5',
          registrations: '150',
          editable: false
        },
        {
          id: 2,
          name: 'wp',
          position: 'https://demo.deneme.com/ib/links/go/64',
          clicks: '5',
          registrations: '150',
          editable: false
        },
        {
          id: 3,
          name: 'wp',
          position: 'https://demo.deneme.com/ib/links/go/64',
          clicks: '5',
          registrations: '150',
          editable: false
        },
        {
          id: 4,
          name: 'wp',
          position: 'https://demo.deneme.com/ib/links/go/64',
          clicks: '5',
          registrations: '150',
          editable: false
        },
        {
          id: 5,
          name: 'wp',
          position: 'https://demo.deneme.com/ib/links/go/64',
          clicks: '5',
          registrations: '150',
          editable: false
        }
      ]
    }
  }
}
</script>

<style>
.x{
  color:#495057;
  font-size: 16px;
}
.z{
    width: 130px;
    height: 45px;
    background-color: #E9EDF4;
    padding-top: 7px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 5%;
    border-top-right-radius: 0%;
    border-bottom-right-radius: 0%;
    border: 1.5px solid #E9EDF4;
    border-right: 1px solid transparent;
}
.text-f{
  font-size: 11px;
}
</style>
